body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.UpperPageContainer{
    position: absolute;
    width: 100vw;
    height: 100vh;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 90px;
    color: white;
    display: grid;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    grid-template-columns: 55% 45%;
    grid-template-rows: 20vh 1fr;
    background-image: url('https://render.fineartamerica.com/images/rendered/default/print/8.000/6.000/break/images-medium-5/dry-cracked-dirt-lane-erickson.jpg');
    background-color: rgba(2, 5, 24, 0.973);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    overflow: hidden;
}
.mapSide, .advertisementSide{
    position: relative;
    height: 100%;
    margin: 1rem;
}
.mapSide{
    display: grid;
    grid-template-columns: 775px 1fr;
    grid-gap: 2rem;
    height: 100%;
}
.mapSide img{
    width: auto;
    max-height: 99%
}
.blackridge{
    box-sizing: border-box;
    padding-left: 30%;
    color: rgba(243, 240, 255, 0.747);
    vertical-align: middle;
}
.gsemLogo{
    width: 40%;
    height: 10vh;
    margin: 100px auto;
    background-image: url(https://gsemtechnologies.com/wp-content/uploads/2018/01/SimustreamWebHorizontal-website-550x400.png);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.suite{
    font-size: 3.6rem;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: rgb(0, 0, 0);
    padding: .3rem .7rem; 
    margin-bottom: .9rem;
    border: .1rem white outset;
    border-radius: .5rem;
}
.q4 .suite{
    height: 100%;
}
.q2>h6:last-of-type{
    margin-top: 1em;
}
.q3{
    position: relative;
    grid-column: span 4;
    background-repeat: no-repeat;
    background-size: contain;
}
h6{
    color: white;
    margin: 0 0 .2rem;
}
.q4{
    grid-column: span 4;
}
.q5{
    grid-column: span 2;
}
.advertisementSide div{
    font-size: 3rem;
}

iframe{
    box-shadow: 10px 10px 10px rgb(0, 0, 0), 50px 50px 300px rgba(51, 51, 51, 0.507); 
    width: 100%;
    height: 50vmin;
}
.LowerPageContainer{
    position: absolute;
    width: 100vh;
    height: 100vw;
    -webkit-transform: rotate(90deg) translateX(-22vw) translateY(-38.6vh);
            transform: rotate(90deg) translateX(-22vw) translateY(-38.6vh);
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    font-size: 90px;
    color: white;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    background-image: url('https://render.fineartamerica.com/images/rendered/default/print/8.000/6.000/break/images-medium-5/dry-cracked-dirt-lane-erickson.jpg');
    background-color: rgba(2, 5, 24, 0.973);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
}
.LowerPageContainer .gsemLogo{
    padding-bottom: 5vw;
    width: 33%;
}
.advertisementSide_lower div{
    padding-left: 100px;
    font-size: 50px;
}
.mapSideVertical{
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin: 3rem;
    height: 100%;
    grid-gap: 3rem;
}
.blackridge_lower{
    margin: 4rem auto 2rem;
    font-size: 90px;
}
.mapSideVertical img{
    width: 89%;
    max-height: 100%;
}
